
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

html {
  background-image: linear-gradient(45deg, #e3e0e0a1, transparent);
}
.carousel-3d-slide {
  -webkit-box-shadow: -1px 15px 20px 0px;
          box-shadow: -1px 15px 20px 0px;
  border-color: #00000005;
}
.carousel-3d-controls a span {
  color: #3333334a;
}
